import React, { useState, useEffect } from 'react';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import './Trend.css';

function Trend() {
  const [memos, setMemos] = useState([]);
  const [title, setTitle] = useState('');
  const [link, setLink] = useState('');
  const [content, setContent] = useState('');
  const [trendScore, setTrendScore] = useState(0);
  const [image, setImage] = useState(null);
  const [editingMemo, setEditingMemo] = useState(null);

  useEffect(() => {
    const savedMemos = JSON.parse(localStorage.getItem('trendMemos')) || [];
    setMemos(savedMemos);
  }, []);

  const saveMemos = (newMemos) => {
    localStorage.setItem('trendMemos', JSON.stringify(newMemos));
    setMemos(newMemos);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!title || !content) return;
    const newMemo = {
      id: Date.now().toString(),
      title,
      link,
      content,
      trendScore,
      image: image ? URL.createObjectURL(image) : null
    };
    saveMemos([newMemo, ...memos]);
    setTitle('');
    setLink('');
    setContent('');
    setTrendScore(0);
    setImage(null);
  };

  const handleEdit = (memo) => {
    setEditingMemo(memo);
    setTitle(memo.title);
    setLink(memo.link);
    setContent(memo.content);
    setTrendScore(memo.trendScore);
    setImage(memo.image);
  };

  const handleUpdate = (e) => {
    e.preventDefault();
    if (!title || !content) return;
    const updatedMemos = memos.map(memo =>
      memo.id === editingMemo.id ? { ...memo, title, link, content, trendScore, image } : memo
    );
    saveMemos(updatedMemos);
    setEditingMemo(null);
    setTitle('');
    setLink('');
    setContent('');
    setTrendScore(0);
    setImage(null);
  };

  const handleDelete = (id) => {
    if (window.confirm('   ?')) {
      const updatedMemos = memos.filter(memo => memo.id !== id);
      saveMemos(updatedMemos);
    }
  };

  const onDragEnd = (result) => {
    if (!result.destination) return;
    const items = Array.from(memos);
    const [reorderedItem] = items.splice(result.source.index, 1);
    items.splice(result.destination.index, 0, reorderedItem);
    saveMemos(items);
  };

  const moveItem = (id, direction) => {
    const index = memos.findIndex(memo => memo.id === id);
    if ((direction === 'up' && index === 0) || (direction === 'down' && index === memos.length - 1)) return;
    const newIndex = direction === 'up' ? index - 1 : index + 1;
    const newMemos = Array.from(memos);
    const [movedItem] = newMemos.splice(index, 1);
    newMemos.splice(newIndex, 0, movedItem);
    saveMemos(newMemos);
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file && file.size <= 10 * 1024 * 1024) {  // 10MB limit
      setImage(file);
    } else {
      alert('  10MB  .');
    }
  };

  return (
    <div className="trend-page">
      <h1> </h1>
      <form onSubmit={editingMemo ? handleUpdate : handleSubmit} className="memo-form">
        <input
          type="text"
          placeholder=""
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          required
        />
        <input
          type="url"
          placeholder=" "
          value={link}
          onChange={(e) => setLink(e.target.value)}
        />
        <textarea
          placeholder=" (1,000 )"
          value={content}
          onChange={(e) => setContent(e.target.value)}
          maxLength={1000}
          required
        />
        <input
          type="file"
          accept="image/*"
          onChange={handleImageChange}
        />
        <div className="trend-score">
          <label> :</label>
          {[1, 2, 3, 4, 5].map((star) => (
            <span
              key={star}
              className={`star ${star <= trendScore ? 'filled' : ''}`}
              onClick={() => setTrendScore(star)}
            >
              
            </span>
          ))}
        </div>
        <button type="submit">
          {editingMemo ? ' ' : ' '}
        </button>
        {editingMemo && (
          <button type="button" onClick={() => setEditingMemo(null)}></button>
        )}
      </form>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="memos">
          {(provided) => (
            <div {...provided.droppableProps} ref={provided.innerRef} className="memo-list">
              {memos.map((memo, index) => (
                <Draggable key={memo.id} draggableId={memo.id} index={index}>
                  {(provided) => (
                    <div
                      ref={provided.innerRef}
                      {...provided.draggableProps}
                      {...provided.dragHandleProps}
                      className="memo-item"
                    >
                      <div className="memo-order-buttons">
                        <button onClick={() => moveItem(memo.id, 'up')}>
                          <svg width="24" height="24" viewBox="0 0 24 24">
                            <path d="M7 22l5-5 5 5z"/>
                          </svg>
                        </button>
                        <button onClick={() => moveItem(memo.id, 'down')}>
                          <svg width="24" height="24" viewBox="0 0 24 24">
                            <path d="M7 5l5 5 5-5z"/>
                          </svg>
                        </button>
                      </div>
                      <h2 className="memo-title">{memo.title}</h2>
                      {memo.image && (
                        <img src={memo.image} alt=" " className="memo-image" />
                      )}
                      {memo.link && (
                        <a 
                          href={memo.link} 
                          className="memo-link" 
                          target="_blank" 
                          rel="noopener noreferrer"
                          title={memo.link}
                        >
                          {memo.link.length > 100 ? `${memo.link.substring(0, 100)}...` : memo.link}
                        </a>
                      )}
                      <div className="memo-content-wrapper">
                        <p className="memo-content">{memo.content}</p>
                      </div>
                      <div className="memo-trend-score">
                        {[1, 2, 3, 4, 5].map((star) => (
                          <span key={star} className={`star ${star <= memo.trendScore ? 'filled' : ''}`}>
                            
                          </span>
                        ))}
                      </div>
                      <div className="memo-actions">
                        <button onClick={() => handleEdit(memo)}></button>
                        <button onClick={() => handleDelete(memo.id)}></button>
                      </div>
                    </div>
                  )}
                </Draggable>
              ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>
    </div>
  );
}

export default Trend;